/* App.css */
header {
  background-color: black;
  color: white;
  padding: 10px;
}

header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .social-links {
  display: flex;
  gap: 20px;
}

header ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

section {
  padding: 50px;
}

.landing {
  text-align: center;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.project-card {
  border: 1px solid #ddd;
  padding: 10px;
}
